
.sendForm {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;

}

.textarea {
    width: 976px;
    min-height: 40px;
    background: #F5F7FB;
    box-shadow: 0 5px 20px rgba(29, 33, 38, 0.03), 0 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 25px;
    padding: 5px 20px;
}

.button {
    width: 120px;
    height: 40px;
    box-shadow: 0 0 7px 2px rgba(33, 42, 52, 0.58);

    background: #0066CC;
    border-radius: 20px;
    border: none;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    /* identical to box height */


    color: #FFFFFF;
}

.button:hover {
    box-shadow: none;
    border: 1px solid rgba(33, 42, 52, 0.58);

}

.button:active {
    background-color: #cc0000;

}
