.greetingForm {

}

.inputAndButtonContainer {
    display: flex;
    gap: 12px;
}

.error {
    margin-top: 15px;
    font-size: 14px;
    color: #CC0000;
}

.input {
    width: 372px;
    height: 36px;
    border-radius: 5px;
    border: 1px solid #d1d1d1;

}

.input:focus {
    outline: none;
}

.errorInput {
    border: 2px solid #cc0000;

}

.button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
    padding: 5px 24px;
    cursor: pointer;

    width: 78px;
    height: 36px;

    color: #ffffff;
    font-size: 14px;
    font-weight: 600;
    text-transform: capitalize;

    background: #0066CC;
    opacity: 0.5;
    border-radius: 5px;
}

.button:focus {
    /*outline: none;*/
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
    background-color: #045cb7;
}


.button:active {

}

.button:disabled {
    opacity: 0.5;
}

.text {
    color: rgba(0, 0, 0, 0.5);
    font-size: 14px;
    line-height: 17px;
    padding-bottom: 9px;
}

